export default {
  data () {
    return {
      showLifeCircleLog: true,
      intervalHandlers: []
    }
  },
  beforeCreate () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} beforeCreate =====`)
  },
  created () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} created =====`)
  },
  beforeMount () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} beforeMount =====`)
  },
  mounted () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} mounted =====`)
  },
  // beforeUpdate () {
  //   console.log(`===== ${this.$route.path} beforeUpdate =====`)
  // },
  // updated () {
  //   console.log(`===== ${this.$route.path} updated =====`)
  // },
  beforeDestroy () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} beforeDestroy =====`)
    this.clearIntervals()
  },
  destroyed () {
    if (this.showLifeCircleLog) console.log(`===== ${this.$route.path} destroyed =====`)
  },
  methods: {
    addIntervalHandler (func, time) {
      let handler = setInterval(func, time)
      this.intervalHandlers.push(handler)
    },
    clearIntervals () {
      for (let handler in this.intervalHandlers) {
        clearInterval(handler)
      }
    }
  }
}
