<template>
  <div class="demo-page svg-demo">
    <el-row>

      <el-col :span="24">

        <div class="svg__wrapper" >
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <polyline :points="computedPoints" style="fill:white;stroke:red;stroke-width:1" />
          </svg>
        </div>

        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <polyline points="20,20 40,25 60,40 80,120 120,140 200,180"
                      style="fill:none;stroke:black;stroke-width:3" />
          </svg>
        </div>

        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border">
            <polyline points="0,40 40,40 40,80 80,80 80,120 120,120 120,160" style="fill:white;stroke:red;stroke-width:4" />
          </svg>
        </div>


      </el-col>
    </el-row>

  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
export default {
  name: 'SVGDemo006',
  mixins: [DemoPageMixin],
  data () {
    return {
      step: 5,
      pointIndex: 0,
      points: []
    }
  },
  created () {
    this.addIntervalHandler(this.addPoint, 150)
  },
  methods: {
    addPoint () {
      // test
      this.pointIndex = this.pointIndex + 1
      const maxCnt = (300 / this.step)
      const point = { index: this.pointIndex, x: 300, y: 150 }
      if ((this.pointIndex + 1) % 5 === 0) {
        point.y = 200
      }
      if (this.pointIndex % 5 === 0) {
        point.y = 100
      }
      this.points.push(point)
      if (this.points.length > maxCnt) {
        this.points = this.points.slice(1)
      }
      if (this.pointIndex === maxCnt) {
        this.points.forEach((cur) => {
          cur.x = cur.x - this.pointIndex * this.step
        })
        this.pointIndex = 0
      }
    }
  },
  computed: {
    computedPoints () {
      const pointStrs = []
      let offset = this.pointIndex * this.step
      this.points.forEach((cur) => {
        pointStrs.push(`${cur.x + cur.index * this.step - offset},${cur.y}`)
      })
      return pointStrs.join(' ')
    }
  }
}
</script>

